	module.exports = {"data":{"post":{"html":"<p>If you haven't noticed, I've been having way too much fun with my quick links. It could be that you haven't noticed, since they are not (yet) in my RSS feed and I don't ping weblogs.com etc. (yet) when I add a new one. Nor are they archived (yet). I'd like to integrate them into the main column, but I'm still trying to come up with the magical combination of MT markup and plugins that will let me format them as I want:</p>\n<p>Date headline<br />\nQuicklinks for the day as a simple list (no title or footer)<br />\nAll other posts (with title and footer)</p>\n<p><b>Update</b> I couldn't do it with MT alone, but with some PHP looping in the MTDateHeader, I think I've got it, at least the layout on the index page.</p>","fields":{"title":"More Fun With Quick Links","date":"07 May 2003","url":"https://papascott.de/2003-05-07-more-fun-with-quick-links/"}}},"pathContext":{"slug":"/2003-05-07-more-fun-with-quick-links/","prev":{"fields":{"title":"Instapundit Poetry","date":"07 May 2003","slug":"/2003-05-07-instapundit-poetry/"}},"next":{"fields":{"title":"Baltic Blog dines with the Prime Minister of Eston","date":"08 May 2003","slug":"/2003-05-08-baltic-blog-dines-with-the-prime-minister-of-eston/"}}}}

